import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/index',
            name: 'index',
            component: () =>
                import ('./views/Index.vue')
        },
        {
            path: '/film',
            name: 'film',
            component: () =>
                import ('./views/Film.vue'),
            redirect: '/film/index',
            children: [{
                path: '/film/index',
                name: 'filmIndex',
                component: () =>
                    import ('./views/film/FilmIndex.vue'),
            }, {
                path: '/film/list',
                name: 'filmList',
                component: () =>
                    import ('./views/film/FilmList.vue'),
            }, {
                path: '/film/cinema',
                name: 'filmCinema',
                component: () =>
                    import ('./views/film/FilmCinema.vue'),
            }]
        },
        {
            path: '/express',
            name: 'express',
            component: () =>
                import ('./views/Express.vue')
        },
        {
            path: '/recipe',
            name: 'recipe',
            component: () =>
                import ('./views/Recipe.vue')
        },
        {
            path: '/weather',
            name: 'weather',
            component: () =>
                import ('./views/Weather.vue')
        },
        {
            path: '/composition',
            name: 'composition',
            component: () =>
                import ('./views/Composition.vue')
        },
        {
            path: '/airQuality',
            name: 'airQuality',
            component: () =>
                import ('./views/AirQuality.vue')
        },
        {
            path: '*',
            redirect: '/index'
        }

    ]
})


export default router;